/* STATIC STYLES (desktop-first) */

.portfolio {
  display: flex;
  flex-direction: column;
  gap: 4rem;
  position: relative;
  padding-inline: 3rem;
  width: 100vw;
}

.portfolio-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1rem;
}

.earth-container {
  width: 100%;
  height: 30rem;
  overflow: visible;
}

